* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

img {
  box-sizing: border-box;
  border-radius: 10px;
  margin: 0;
  padding: 0;
}

img:hover {
  transition-duration: .4s;
  transform: scale(1.05);
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  list-style: none;
}

.search-form {
  box-sizing: border-box;
  background-color: #7ce4f9;
  height: 50px;
  display: flex;
  position: relative;
}

.search-form input {
  background-color: #fff;
  border: none;
  border-radius: 3px;
  width: 550px;
  height: 30px;
  margin: 10px auto;
  padding: 10px 40px 10px 10px;
}

.search-form input:focus {
  color: #722ff7;
  outline: 2px solid #a8a2ff;
}

.submit-button {
  cursor: pointer;
  color: #454648;
  background-color: #fff;
  border: none;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 20%;
  left: 65%;
}

.submit-button:hover {
  color: #454648;
}

.btn {
  cursor: pointer;
  background-color: #0000;
  border: none;
  width: 30px;
  height: 30px;
}

.btn:hover, .is-active {
  color: #fff;
}

.icons {
  justify-content: center;
  align-items: center;
  display: flex;
}

.svg {
  fill: currentColor;
  width: 15px;
  height: 15px;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  padding: 15px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 8.75px);
  position: relative;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.info {
  background-color: #c3cac3;
  justify-content: space-around;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.info:hover {
  background-color: #31d0aa;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.button-holder, .button-load {
  justify-content: center;
  width: 100%;
  display: flex;
}

.load-more {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #3b5998;
  border: none;
  border-radius: 3px;
  margin-bottom: 10px;
  padding: 10px 20px;
  font-size: 15px;
  display: none;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.load-more:hover {
  background-color: #a2c0ff;
}

.is-visible {
  display: flex;
}
